import marked from 'marked'
import blog from '@/api/blog'


export default {
    data() {
        return {
            title: '',//标题
            rawContent: '',//内容
            user: {//用户信息 
            },
            createdAt:''//发布时间
        }
    },
    created() {
        //如何获取博客id：在路由 router 中 path: '/detail/:blogId' 存放用户的id
        //使用params方式传递路由参数 方法：this.$route.params.name
        this.blogId = this.$route.params.blogId
        
        console.log(this.blogId) //打印出博客的id
        blog.getDetail({ blogId: this.blogId }).then(res => {
            console.log(res)
            this.title = res.data.title
            this.rawContent = res.data.content
            this.createdAt = res.data.createdAt
            this.user = res.data.user
        })
       
    },
    computed: {//计算属性
        markdown() {
            return marked(this.rawContent) // 导入 markdown 语法 包装内容
        }
    }
}


